import React from "react"
import CookieConsent, { Cookies } from "react-cookie-consent"
import { Link } from "gatsby"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import { useLocation } from "@reach/router"
import { initializeAndTrack } from "gatsby-plugin-gdpr-cookies"
import cookieStyle from "./cookie.module.css"

const CustomBanner = (props) =>{

    let location = useLocation();

    return(
        <Row className={"presentation no-margin"}>
            <Col>
                <CookieConsent
                    disableStyles={true}
                    location={"none"}
                    overlay={true}
                    overlayStyle={{position: "fixed",
                        left: 0,
                        top: 0,
                        width: "100%",
                        height: "100%",
                        zIndex: "998",
                        backgroundColor: "rgba(0,0,0,0.4)"
                    }}
                    containerClasses={cookieStyle.cookieBox}
                    buttonText="Accept and Close"
                    onAccept={() => {
                        initializeAndTrack(location)
                        Cookies.set("gatsby-gdpr-google-tagmanager", true)
                        Cookies.set("gatsby-gdpr-facebook-pixel", true)
                    }}
                    buttonClasses={cookieStyle.acceptButton}
                    enableDeclineButton={true}
                    declineButtonText="More Information"
                    onDecline={() => {
                        console.log("More information Pressed")
                    }}
                    declineButtonClasses={cookieStyle.infoButton}
                    contentClasses={cookieStyle.contentPosition}

                    cookieName="gatsby-gdpr-google-analytics"
                    expires={365}
                    acceptOnScroll={true}
                    acceptOnScrollPercentage={15}
                >
                    <Row className={"presentation no-margin"}>
                        <Col >
                            <h4>We use cookies to improve your experience</h4>
                            <p>We use cookies essential for this site to function well. Please click "Accept" to help us improve its usefulness with additional cookies. Learn about our use of cookies, and collaboration with trusted analytics partners in our <Link to={"/privacy/"}>Privacy Policy</Link>.</p>
                        </Col>
                    </Row>
                </CookieConsent>
            </Col>
        </Row>
    )
};

export default CustomBanner;
